import {DebugContext} from './view';

export const /** @type {?} */ ERROR_TYPE = 'ngType';
export const /** @type {?} */ ERROR_COMPONENT_TYPE = 'ngComponentType';
export const /** @type {?} */ ERROR_DEBUG_CONTEXT = 'ngDebugContext';
export const /** @type {?} */ ERROR_ORIGINAL_ERROR = 'ngOriginalError';
/**
 * @param {?} error
 * @return {?}
 */
export function getType(error: Error): Function {
  return ( /** @type {?} */((error as any)))[ERROR_TYPE];
}
/**
 * @param {?} error
 * @return {?}
 */
export function getDebugContext(error: Error): DebugContext {
  return ( /** @type {?} */((error as any)))[ERROR_DEBUG_CONTEXT];
}
/**
 * @param {?} error
 * @return {?}
 */
export function getOriginalError(error: Error): Error {
  return ( /** @type {?} */((error as any)))[ERROR_ORIGINAL_ERROR];
}
